@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Black+Ops+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ceviche+One&display=swap);
@font-face {
    font-family: L2;
    src: url(/static/media/l2-font.4a209cc0.ttf);
}

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

a{
    text-decoration: none;
}

ul{
    list-style: none;
}

li{
    list-style: none;
}

body{
    font: 400 14px 'Ceviche One','Black Ops One', Roboto, sans-serif;
    -webkit-font-smoothing: antialiased;
    /* background-image: url(./assets/background.jpg); */
    background-size: cover;
    background-repeat: no-repeat;
}

input, button, textarea{
    font: 400 18px Roboto, sans-serif;
}
.container-header{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.logo-img img{
  max-width: 80px;
  height: auto;
  margin: 8px;
}

.menu-principal a{
  font-size: 23px;
  /* font-weight: bold; */
  color: white;   
  -webkit-transition: 0.4s, text-shadow 0.4s;   
  transition: 0.4s, text-shadow 0.4s;
}

.menu-principal a:hover{
  color:#ffbb00;
}

.menu-principal li{
  display: inline;
  margin-left: 30px;
}

.menu-users{
  margin: 8px 0;
  display: flex;
  margin-left: auto;
  flex-wrap: wrap;
  margin-right: 30px;
}

.button{
  background-color: transparent;
  padding: 5px 0;
  border: 2px solid #ffbb00;
  border-radius: 8%;
  margin-right: 10px;    
}

.button a{
  font-size: 23px;
  /* font-weight: bold; */
  padding: 5px 10px;
  color: #ffbb00;
}

.button a:hover{
  background-color: #ffbb00;
  color: #000000;
}
.container-playnow{
	width: 100%;
  height: 745px;
	background-repeat: no-repeat;
	display: flex;
	justify-content: center;
	align-items: center;
}

.container-playnow .container-menu-left{
	width: 10%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: auto;
}

.container-playnow .container-menu-left .menu{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin: 50px 0;
}

.container-playnow .container-menu-left .imgbottom img{
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.container-playnow .container-menu-left .menu li{
	margin: 15px 0;
}

.container-playnow .container-menu-left .menu a{
	font-size: 15px;
	font-family: Roboto, sans-serif;
	font-weight: bold;
	color: #535353;
}

.container-playnow .container-menu-left .menu a:hover{
	color: #fff;
}

.container-playnow .btn-play{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	width: 80%;
}

.container-playnow .btn-play img{
	margin-bottom: 1%;
}

.container-playnow .btn-play a{
	color:#fff;
	font-size: 32px;
	text-shadow:  0 0 1em rgba(0, 0, 0, 0.9);
}

.container-playnow .btn-play .myButton {
	background-color: rgba(255, 236, 100, 0.7);
	border-radius: 6px;
	padding: 8px 70px;
	margin: 0 auto;
}


.container-playnow .btn-play .myButton:hover {
	background-color:rgba(255, 236, 100, 0.9);
}

.container-playnow .btn-play .interlude-logo{
	display: flex;
	justify-content: center;
	align-items: center;
}

.container-playnow .btn-play .interlude-logo img{
	max-width: 30%;
	height: auto;
	margin-bottom: 20%;
}

.container-playnow .container-menu-right{
	width: 10%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: auto;
}

.container-playnow .container-menu-right .menu{
	margin: 50px 0;
}

.container-playnow .container-menu-right .imgbottom img{
	-webkit-transform: rotate(180deg);
	        transform: rotate(180deg);
}

.container-playnow .container-menu-right .menu li{
	margin: 15px 0;
}

.container-playnow .container-menu-right .menu a{
	font-size: 15px;
	font-family: Roboto, sans-serif;
	font-weight: bold;
	color: #535353;
}

.container-playnow .container-menu-right .menu a:hover{
	color: #fff;
}

.bodyHome{
    background-size: 100% auto;
    margin: 0;
    padding: 0;
    background-image: url(/static/media/back.2b87af3a.png);
    background-repeat: no-repeat;
    background-color:black;
    min-height: 3000px;
    
}

.content-register{
  width: 960px;
  margin: 8% auto 0 auto;
}

.container-register .content-register form {
  display: flex;
  flex-direction: column;
}

.container-register .content-register form h1{
  font-size: 42px;
  text-align: center;
  margin-bottom: 30px;
}

.container-register .content-register form input{
  width: 50%;
  margin: 0 auto;
  border: 1px solid #ccc;
  padding: 12px;
  margin-top: 8px;
}

.container-register .content-register form button{
  width: 50%;
  margin: 20px auto 0 auto;
  border: 2px solid #ffbb00;
  padding: 12px;
  font-size: 20px;
  background-color: transparent;
  font-weight: bold;
  color: #ffbb00; 
  cursor: pointer;
}

.container-register .content-register form button:hover{
  color: #000;
  background-color: #ffbb00;
}

.container-register .content-register .links-register {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.container-register .content-register .links-register a{
  color: #000;
  font-size: 20px;
}

.container-register .content-register .links-register a:hover{
  color: #ffbb00;
}

.container-register .content-register .links-register .back-link-register{
  display: flex;
  align-items: center;
  margin: 2% 0 0 24.8888%;
}

.container-register .content-register .links-register .back-link-register .arrow-left-register{
  margin-right: 5px;
}

.container-register .content-register .links-register .foward-link-register{
  display: flex;
  align-items: center;
  margin: 2% 24.8888% 0 0;
}

.container-register .content-register .links-register .foward-link-register .arrow-right-register{
  margin-left: 5px;
}

.container-register .content-register .radio-button-register{
  display: flex;
  align-items: center;
}

.container-register .content-register .radio-button-register input{
  margin-right: -23%;
}

.loading-icon-container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 19%;
}

.loading-icon-container img{
  max-width: 5%;
  height: auto;
}
