@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Black+Ops+One&display=swap');
@import url('https://fonts.googleapis.com/css?family=Ceviche+One&display=swap');

@font-face {
    font-family: L2;
    src: url(../src/assets/fonts/l2-font.ttf);
}

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

a{
    text-decoration: none;
}

ul{
    list-style: none;
}

li{
    list-style: none;
}

body{
    font: 400 14px 'Ceviche One','Black Ops One', Roboto, sans-serif;
    -webkit-font-smoothing: antialiased;
    /* background-image: url(./assets/background.jpg); */
    background-size: cover;
    background-repeat: no-repeat;
}

input, button, textarea{
    font: 400 18px Roboto, sans-serif;
}